@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
     "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    box-sizing: border-box;
}
.wrap{
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
}
.flex{
    display: flex;
} 
.jc-between{
justify-content: space-between;
}
.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer;-webkit-transition:.3s all ease;transition:.3s all ease}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.custom-button{min-width:1vmax;width:-webkit-max-content;width:-moz-max-content;width:max-content;height:-webkit-max-content;height:-moz-max-content;height:max-content;letter-spacing:.0892857143em;line-height:1em;padding:.6em 1em;font-size:.8em;background-color:black;color:white;text-transform:uppercase;font-family:Roboto, sans-serif;font-weight:700;cursor:pointer;display:flex;justify-content:center;-webkit-transition:.1s all ease-in-out;transition:.1s all ease-in-out;border:1.5px solid black;border-radius:5px}.custom-button:hover{background-color:rgba(255,255,255,0);color:black}.custom-button.google-sign-in{border:1.5px #4285f4 solid;background-color:rgba(255,255,255,0);color:#4285f4}.custom-button.google-sign-in:hover{background-color:#4285f4;color:#fff}.custom-button.inverted{background:rgba(255,255,255,0.4);color:black;font-size:5vmax}.custom-button.inverted:hover{background-color:black;color:white}

.cart-item{width:100%;display:flex;margin-bottom:.5vmax;border-top:1px solid #000;padding-top:.5vmax}.cart-item img{width:30%}.cart-item:first-of-type{border-top:none}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:.3vmax .4vmax}.cart-item .item-details .name{padding-left:2vw;font-size:.8vmax}@media screen and (max-width: 813px){.cart-item .item-details .name{font-size:4vw}}

.cart-dropdown{position:absolute;width:15vmax;height:20vmax;display:flex;flex-direction:column;padding:1vmax;border:1px solid black;background-color:white;top:5vmax;right:5vw;z-index:5;-webkit-transition:.5s all ease;transition:.5s all ease}@media screen and (max-width: 813px){.cart-dropdown{top:16.2vw;width:100vw;height:92vh;position:fixed;left:0}}.cart-dropdown .cart-items{display:flex;flex-direction:column;overflow-y:scroll;margin-bottom:.8vmax}@media screen and (max-width: 813px){.cart-dropdown .cart-items{height:auto;font-size:2vmax}}.cart-dropdown .empty-message{font-family:'Lato', sans-serif;font-weight:300;margin:auto;font-size:1vmax}@media screen and (max-width: 813px){.cart-dropdown .empty-message{font-size:2vmax}}.cart-dropdown button{height:-webkit-max-content;height:-moz-max-content;height:max-content;margin-top:auto;margin-bottom:1vh}

header{background:rgba(255,255,255,0.884);height:5vmax;display:flex;align-items:center;padding-left:10vw;padding-right:10vw;position:fixed;top:0;width:100vw;z-index:10;box-shadow:0 0 15px #00000094;justify-content:space-between}@media screen and (max-width: 813px){header{height:16vw}}header .logo{font-family:'Roboto', sans-serif;text-decoration:none;color:#444444;font-size:3vmax}header .option{cursor:pointer}header .title{display:flex}header .title p{color:#333;font-family:'Ubuntu', sans-serif;font-weight:500;font-size:.9vmax;text-transform:uppercase;margin-top:auto;margin-bottom:.1vmax}header .navigation-menu ul{display:flex;list-style-type:none}header ul{display:flex}@media screen and (max-width: 568px){header ul{padding-left:20px}}header li{font-size:1.2rem;margin-left:1em;font-weight:300;align-items:center;display:flex;width:-webkit-max-content;width:max-content;width:-moz-max-content}@media screen and (max-width: 813px){header li{font-size:1em}header li:first-of-type{margin:0}header li:last-of-type{margin-right:1em}}header li a{width:-webkit-max-content;width:max-content;width:-moz-max-content;text-decoration:none;color:#333;font-weight:400}header li a:hover{color:#000}#header-space{min-height:5vmax;visibility:hidden}@media screen and (max-width: 813px){#header-space{min-height:16vw}}

.errorPage{background-color:#EFEDFA;padding-top:3vw;padding-left:20vw;padding-right:20vw;min-width:100vw;min-height:calc(100vh - 5.2vmax);align-content:center;color:#9F6C66}@media screen and (max-width: 813px){.errorPage{min-height:calc(100vh - 16vw);padding-top:25vw;padding-left:5vw;padding-right:5vw}}.errorPage img{display:block;width:23vmax;margin:.2vmax auto .5vmax auto}.errorPage h1{font-size:2em;font-weight:600;text-align:center;margin-bottom:15px}@media screen and (max-width: 813px){.errorPage h1{font-size:1.5em}}.errorPage p{font-size:1em;text-align:justify}@media screen and (max-width: 813px){.errorPage{width:85vw}}

